___CSS_LOADER_EXPORT___.push([module.id, "\n.margin[data-v-6f76e239] {\n    margin-left: 30px;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/studio/savePlus.vue"],"names":[],"mappings":";AA4DA;IACI,iBAAiB;AACrB","sourcesContent":["<script setup lang=\"ts\">\nimport { computed } from '@vue/reactivity';\nimport { ref } from 'vue';\nimport { container } from '../../../../../domain/core/diContainer';\nimport { IConfig } from '../../../../../domain/infrastructure/IConfig';\nimport { IStudio } from '../../../../../domain/useCase/IStudio';\nimport { IUseCaseExecutor } from '../../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModel } from '../../../../../domain/viewModel/IViewModel';\n\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst config = container.resolve<IConfig>(IConfig);\n\nconst currentItem = viewModel.studio.currentItem!;\n\nconst checkin = ref(false);\nconst buttonName = computed(() => checkin.value ? \"Check-in\" : \"Save\");\nconst version = ref<\"major\" | \"minor\">(\"minor\");\nconst comment = ref({ short: \"\", long: undefined, hasLong: false });\nconst emit = defineEmits([\"close\"]);\n\nconst commentMaxLength = config.getValue(\"studio\").commentMaxLength;\n\n\nfunction save() {\n    executor.execute(async () => {\n        if (checkin.value) {\n            await studio.checkin(currentItem, { shortComment: comment.value.short, version: version.value, longComment: comment.value.hasLong ? comment.value.long : undefined });\n        } else {\n            await studio.save(currentItem);\n        }\n        emit(\"close\");\n    }, { loading: true });\n}\n\n</script>\n<template>\n    <div>\n        <div>Saving model {{ currentItem.name }}</div>\n        <div>Checkin: <input type=\"checkbox\" v-model=\"checkin\"></div>\n        <template v-if=\"checkin\">\n            <div>Short Comment: <input type=\"text\" :maxlength=\"commentMaxLength\" v-model=\"comment.short\"></div>\n            <div>Long Comment: <input type=\"checkbox\" v-model=\"comment.hasLong\"> </div>\n            <template v-if=\"comment.hasLong\">\n                <div class=\"margin\">\n                    <textarea v-model=\"comment.long\"></textarea>\n                </div>\n            </template>\n            <div>Version</div>\n            <div class=\"margin\">\n                <div>Major: <input type=\"radio\" value=\"major\" v-model=\"version\"> </div>\n                <div>Minor: <input type=\"radio\" value=\"minor\" v-model=\"version\"> </div>\n            </div>\n        </template>\n        <div><button @click=\"save()\">{{ buttonName }}</button></div>\n    </div>\n</template>\n<style scoped>\n.margin {\n    margin-left: 30px;\n}\n</style>"],"sourceRoot":""}]);
